import {
  IInputInfo, ILoginPayload, IExamUrlParams, IFileUrl,
} from '@/types/interface';
import axios from 'axios';

export function getLostPhone(params: IInputInfo): Promise<ILoginPayload> {
  return new Promise((resolve, reject) => {
    axios
      .post('raspapp/Get_LostPhone', params)
      .then((response) => {
        resolve(response.data);
      })
      .catch((error) => {
        reject(error);
      });
  });
}

export function getSnapCode(params: IInputInfo): Promise<void> {
  return new Promise((resolve, reject) => {
    axios
      .post('raspapp/Get_SnapCode', params)
      .then((response) => {
        resolve();
      })
      .catch((error) => {
        reject(error);
      });
  });
}

export function getUploadFileUrl(params: IExamUrlParams): Promise<IFileUrl[]> {
  return new Promise((resolve, reject) => {
    axios
      .post('raspapp/Testhistory/GetUploadFileUrl', params)
      .then((response) => {
        const { data } = response;
        resolve(data.Data);
      })
      .catch((error) => {
        reject(error);
      });
  });
}

export function changeMyPw(params: IInputInfo): Promise<ILoginPayload> {
  return new Promise((resolve, reject) => {
    axios
      .post('raspapp/ChangeMyPW', params)
      .then((response) => {
        resolve(response.data);
      })
      .catch((error) => {
        reject(error);
      });
  });
}
